export default{
	data(){
		return{
			headerTop: 0,
			headHeight:0,
			modalName:'',//是否显示授权弹窗
			userObj: {nickname:'',avatar:'../../static/missing-face.png'},//用户信息
			applicationstatu: '',
		}
	},
	methods:{
		//
		hideModal:function(){
			uni.navigateBack({
				delta:1
			})
		},
		
		closeDoLogin:function(){
			this.modalName = '';
		},
		//跳转到消息列表
		toMessage(){
			uni.navigateTo({
				url:'./message_list'
			})
		},
		//跳转到收藏
		toCollected(){
			uni.navigateTo({
				url:'../commodity/collect'
			})
		},
		//跳转到优惠券
		toDiscount:function(){
			uni.navigateTo({
				url:"./discount"
			})
		},
		//跳转到订单
		toOrder:function(num){
			uni.navigateTo({
				url:'./order?type=' + num
			})
		},
		//跳转到分销
		toDistribution:function(){
		    // application
			// if(this.applicationstatu == '' || this.applicationstatu == null ){
			// 	uni.navigateTo({
			// 		url:'./apply_distribution'
			// 	})
			// 	return
			// }
          	    
			if(this.applicationstatu == '0'  ){
				uni.navigateTo({
					url:'./apply_distribution'
				})
				return
			}	
			
			if(this.applicationstatu == '2' ){
				uni.navigateTo({
					url:'./check'
				})
				return
			} 
			if(this.applicationstatu == '1'){
				uni.navigateTo({
					url:'./distribution'
				})
			}
			
		},
		//跳转到会员
		toMember:function(){

			uni.navigateTo({
				url:"./member"
			})
		},
		//跳转到关于我们
		toAbout:function(){
		
			uni.navigateTo({
				url:'./about'
			})
		},
		//跳转到联系我们
		toConnection:function(){
			uni.navigateTo({
				url:'./connection'
			})
		},
		//跳转到设置
		toSeting(){
			uni.navigateTo({
				url:'./seting'
			})
		},
		//获取分销状态
		getMarketStatu(){
			let self = this;
			let data = {
				"token": uni.getStorageSync('token'),
				"userid": uni.getStorageSync('customer').userid
			};
			this.ask('/app/me/marketStatu','POST',data,function(res){
				self.applicationstatu = res.data.applicationstatu
				console.log(res)
			})	
		}
	},
	onLoad:function(){
		
	},
	onShow() {
		if(uni.getStorageSync('customer') == ''){
			this.modalName = 'doLoginModal';
		}else{
			this.userObj = uni.getStorageSync('customer');
			this.getMarketStatu();
		}
	},
	created() {
		//初始化顶部高度
		this.headerTop = this.StatusBar + 'px';
		this.headHeight = this.CustomBar/2 + 'px';
	}
}
